@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap);
body,
html {
    margin: 0
}

.app-container {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.data-container {
    display: flex;
    justify-content: center;
    width: 100%
}

.message-container {
    font-family: 'Montserrat',
        sans-serif;
    grid-column-end: 3;
    background-color: white;
    border-color: #CCCCCC;
    box-shadow: 0px 0px 9px rgba(66, 66, 66, .75);
    width: 80%;
    height: 80vh;
    border-style: solid;
    margin-top: 1em;

    padding: 10px;
    overflow: scroll;
    overflow-x: hidden;

}

/* width */
::-webkit-scrollbar {
    width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888;

}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}

.logOutNow {
    display: flex;
    justify-content: center;
    background-color: #6ddccf;
    margin: 0;
    padding: 0;
    width: 100%
}

@media only screen and (max-width: 768px) {

    .message-container {
        width: 100%;
        margin-top: 0;
        border-color: white;
    }

    .input {
        height: 12vh;
        background-color: black;
    }

}
body {
    background-color: #94ebcd;
}

@media only screen and (max-width: 768px) {}

.container {
    display: flex;
    justify-content: center;
    align-content: center;
}

.login {
    font-family: 'Montserrat',
        sans-serif;
    margin-top: 5em;
    padding: 10px;
    background-color: #6ddccf;
    border-radius: 10px;
    margin-left: 10px;
    margin-right: 10px;
}


.input-field {
    font-family: 'Montserrat',
        sans-serif;
    padding: 10px;
    font-size: 16px;
    border-width: 1px;
    border-color: #CCCCCC;
    background-color: #FFFFFF;
    color: #000000;
    border-style: solid;
    border-radius: 23px;
    box-shadow: 0px 0px 9px rgba(66, 66, 66, .75);
    text-shadow: 0px 0px 0px rgba(66, 66, 66, .75);
}

.input-field:focus {
    outline: none;
}



.button-login {
    font-family: 'Montserrat',
        sans-serif;
    background: linear-gradient(to bottom, #599bb3 5%, #408c99 100%);
    background-color: #599bb3;
    border-radius: 23px 0px 0px 23px;
    display: inline-block;
    cursor: pointer;
    color: #ffffff;
    font-family: Arial;
    font-size: 17px;
    font-weight: bold;
    padding: 13px 32px;
    text-decoration: none;
    text-shadow: 0px 1px 0px #3d768a;
    margin-right: -5px;
}

.button-login:hover {
    background: linear-gradient(to bottom, #408c99 5%, #599bb3 100%);
    background-color: #408c99;
}

.button-login:active {
    position: relative;
    top: 1px;
}

.button-makeAccount {
    font-family: 'Montserrat',
        sans-serif;
    background: linear-gradient(to bottom, #f5f2f5 5%, #44c7db 100%);
    background-color: #f5f2f5;
    border-radius: 0px 23px 23px 0px;
    display: inline-block;
    cursor: pointer;
    color: #ffffff;
    font-family: Arial;
    font-size: 17px;
    font-weight: bold;
    padding: 13px 32px;
    text-decoration: none;
    text-shadow: 0px 1px 0px #3d768a;

}

.button-makeAccount:hover {
    background: linear-gradient(to bottom, #44c7db 5%, #f5f2f5 100%);
    background-color: #44c7db;
}

.button-makeAccount:active {
    position: relative;
    top: 1px;
}
.container-send {
    width: 70%;
}


.sendMessage-input {
    font-family: 'Montserrat',
        sans-serif;
    grid-column-start: 2;
    padding: 8px;
    font-size: 13px;
    border-width: 1px;
    border-color: #CCCCCC;
    background-color: #FFFFFF;
    color: #000000;
    border-style: solid;
    box-shadow: 0px 0px 9px rgba(66, 66, 66, .75);
    text-shadow: 0px 0px 0px rgba(66, 66, 66, .75);
    resize: vertical;
    margin-top: 20px;
    width: 100%;
}


.sendMessage-input:focus {
    outline: none;
}



